import { sendAjax } from '@/common/js/sendAjax.js';
import config from '@/apiConfig';
const { getOrderDetail} = config.api;
export default {
	data() {
		return {
			isUser:1,
			id:null,
			orderDetail:{}
		}
	},
	onLoad(option) {
		console.log(option.id)
		this.id = option.id;
		this.getOrderDetail()
	},
	methods: {
		
		getOrderDetail(){
			var that = this;
			var id = this.id;
			let infoOpt = {
				url: getOrderDetail  + id,
				type: 'GET',
				data:{}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				console.log(res)
				that.orderDetail = res;
				uni.hideLoading()
			},
			infoCb.beforeSend = () => {
			  uni.showLoading({
			  	title:'加载中'
			  })
			};
			sendAjax(infoOpt, infoCb);
		}
	}
}
